import cmdIcon from "../../components/cmd-icon/cmd-icon.vue"
// import lotusAddress from "../../components/Winglau14-lotusAddress/Winglau14-lotusAddress.vue";
export default{
	components:{
		cmdIcon,
	},
	data() {
			return {
				Tag:[],
				tagHome:"tagHome",
				tagSchool:"tagSchool",
				tagCompany:"tagCompany",
				// 设置默认地址默认打开  default----true表示打开；default-----false表示关闭
				default:true,
				lotusAddressData:{
					visible:false,
					provinceName:'广东省',
					cityName:'广州市',
					townName:'天河区',
				},
				region:'',
				address:"省市区县、乡镇等"
			}
		},
		onLoad() {
	
		},
		methods: {
			//打开picker
			openPicker() {
				this.lotusAddressData.visible = true;
			},
			//回传已选的省市区的值
			choseValue(res){
				//res数据源包括已选省市区与省市区code
				console.log(res);
				this.lotusAddressData.visible = res.visible;//visible为显示与关闭组件标识true显示false隐藏
				//res.isChose = 1省市区已选 res.isChose = 0;未选
				if(res.isChose){
					this.lotusAddressData.provinceName = res.provice;//省
					this.lotusAddressData.cityName = res.city;//市
					this.lotusAddressData.townName = res.town;//区
					this.region = `${res.provice} ${res.city} ${res.town}`; //region为已选的省市区的值
				};
				this.address = this.lotusAddressData.provinceName+this.lotusAddressData.cityName+this.lotusAddressData.townName;
			},
			cancel(){
				this.address = "省市区县、乡镇等"
			},
			save(){
				uni.navigateBack()
			},
			chooseTag(e){
				var tag = e.currentTarget.dataset.name;
				this.Tag.push(tag);
				console.log(this.Tag)
			},
			switchDefault(){
				this.default = !this.default;
				console.log(this.default)
			}
	
		}
	
}
